<template>
  <div class="container">
    <div v-if="Object.keys(lists).length !== 0" style="width: 800px;">
      <div
        v-for="(item, name, index) in handle_data"
        :key="index"
        class="box-card"
      >
        <div class="od-title">
          {{ name }}
        </div>
        <el-card
          class="od-content"
          v-for="(task, index) in item"
          :key="index"
          shadow="hover"
        >
          <el-row :gutter="20">
            <el-col :span="21">
              {{ task.title }}
            </el-col>
            <el-col :span="3" class="od-tool">
              <i class="el-icon-document-add" @click="to_today(task)" />
            </el-col>
          </el-row>
        </el-card>
      </div>
    </div>
    <div v-else class="no-task" style="width: 800px;">
      <i class="el-icon-info" />
      还没有逾期待办事项呢！
    </div>
    <div
      class="more-btn"
      @click="hide = !hide"
      v-if="Object.keys(lists).length > 5"
    >
      <div v-show="hide">
        <i class="el-icon-arrow-down" />
        查看更多
      </div>
      <div v-show="!hide">
        <i class="el-icon-arrow-up" />
        收起
      </div>
    </div>
  </div>
</template>

<script>
import { getOverdueTask, overdueToToday } from "@/api/task.js";

export default {
  data() {
    return {
      lists: {},
      hide: true
    };
  },
  methods: {
    fetch_data() {
      getOverdueTask(this.$store.getters.token).then(response => {
        this.lists = response.data;
      });
    },
    to_today(task) {
      overdueToToday(this.$store.getters.token, {
        task_id: task.task_id
      }).then(() => {
        this.fetch_data();
        this.$notify({
          title: "系统通知",
          message: "已添加到今日待办事项中",
          type: "success"
        });
      });
    }
  },
  computed: {
    handle_data() {
      if (this.hide) {
        let show_item = Object.keys(this.lists).slice(0, 5);
        let show_data = {};
        show_item.forEach(item => {
          show_data[item] = this.lists[item];
        });
        return show_data;
      } else {
        return this.lists;
      }
    }
  },
  created() {
    this.fetch_data();
  }
};
</script>

